@media screen {
}

.tk-proxima-nova {
  font-family: 'proxima-nova', sans-serif;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'proxima-nova', sans-serif !important;
  font-weight: 500;
  font-style: normal;
}

.modal {
  width: 80%;
  max-width: 800px;
  height: 400px;
  overflow: hidden;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.modalImage {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media screen and (max-width: 768px) {
  .modal {
    width: 100vw;
    /* height: 200px; */
  }
}

.curlyText {
  font-family: 'proxima-nova', sans-serif;
  font-weight: 500;
  font-style: normal;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 2pt;
  fill: white;
  /* margin-top: -300px; */
  transform: scale(0.5);
}

.mainText {
  font-family: 'proxima-nova';
  color: white;
  font-size: 28px;
}
